*{
  background-color: rgb(12, 12, 58);
}
.app{
  
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  

}

.term1{

  display:grid;
  grid-template-columns: 60px auto 60px;
  grid-template-rows: 1fr;
  column-gap: 30px;
 
}

.term2{
  grid-column: 2;
  grid-row: 1;
  padding: 10px;
  font-size: 30px;
  color:aqua;
  
}

/* extra */
.term{
  width:330px;
  height: 300px;
  border: 3px solid rgb(125, 11, 112);
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
 
}

