.plus{
    color:rgb(125, 11, 112) ;
    grid-column: 3;
    grid-row: 1;
    font-size: 30px;
    border:2px solid rgb(125, 11, 112);
    background-color: rgb(178, 166, 224);
    border-radius: 7px;
        
}

.minus{
    color:rgb(125, 11, 112) ;
    grid-column: 1;
    grid-row: 1;
    font-size: 30px;
    border:2px solid rgb(125, 11, 112);
    background-color: rgb(178, 166, 224);
    border-radius: 7px;
}
.reset{
    margin: 40px;
    padding: 10px;
    color:rgb(125, 11, 112) ;
    font-size: 20px;
    border:2px solid rgb(125, 11, 112);
    background-color: rgb(178, 166, 224);
    border-radius: 7px;
}

.plus:hover , .minus:hover{
    cursor: pointer;
}

.reset:hover{
    cursor: pointer;
    background-color:rgb(125, 11, 112) ;
    color: rgb(178, 166, 224);
}

.plus:active,.minus:active{

    background-color:rgb(125, 11, 112) ;
    color: rgb(178, 166, 224);
    box-shadow: inset 0px 0px 1px 1px darkgray;
    border: 1px solid rgb(178, 166, 224);
    outline: 2px solid black;
   
}
.reset:active{
    background-color:rgb(125, 11, 112) ;
    color: rgb(41, 8, 174);
}
